.site-wrap .crumbs {
  border-bottom: 2px solid #A0A0A0;
  margin-bottom: 20px;
}
@media print {
  .site-wrap .crumbs {
    display: none;
  }
}
.site-wrap .crumbs.simple {
  border-bottom: none;
  margin-bottom: 20px;
}
.site-wrap .crumbs.simple .breadcrumb {
  padding-bottom: 0;
}
.site-wrap .crumbs .breadcrumb {
  background: none;
  color: #165D8B;
  font-size: 16px;
  margin: 20px 0;
  padding: 0;
  position: relative;
}
@media (max-width: 767px) {
  .site-wrap .crumbs .breadcrumb {
    max-width: calc(100% - 132px);
  }
}
.site-wrap .crumbs .breadcrumb i {
  font-size: 15px;
  left: 0;
  margin-right: 5px;
  position: absolute;
  top: 7px;
}
.site-wrap .crumbs .breadcrumb span {
  color: #3e3d3c;
}
.site-wrap .crumbs .breadcrumb .text {
  display: inline-block;
  line-height: 22px;
  padding-left: 20px;
}
.site-wrap .crumbs .breadcrumb a {
  color: #9ac53c;
}
.site-wrap .crumbs .breadcrumb a:hover, .site-wrap .crumbs .breadcrumb a:focus {
  color: #6aab43;
}
.site-wrap .crumbs .breadcrumb a.home {
  font-size: 0;
  position: relative;
}
.site-wrap .crumbs .breadcrumb a.home::before {
  color: #3e3d3c;
  content: "\e910";
  font-family: icomoon;
  font-size: 24px;
  line-height: 16px;
  vertical-align: -3px;
  -webkit-transition: color 0.3s linear;
  -moz-transition: color 0.3s linear;
  -ms-transition: color 0.3s linear;
  -o-transition: color 0.3s linear;
  transition: color 0.3s linear;
}
.site-wrap .crumbs .breadcrumb a.home:hover::before, .site-wrap .crumbs .breadcrumb a.home:focus::before {
  color: #9ac53c;
}
.site-wrap .viewall-crumb {
  border-left: 1px solid #d8e6f8;
  display: inline-block;
  font-size: 16px;
  margin: 20px 20px 20px 14px;
  padding: 0 0 0 20px;
}
@media (max-width: 991px) {
  .site-wrap .viewall-crumb {
    display: none;
  }
}

.slick-slider .slick-arrow {
  position: absolute;
  height: 70px;
  width: 40px;
  z-index: 1;
  -webkit-transition: background 0.3s linear, color 0.3s linear;
  -moz-transition: background 0.3s linear, color 0.3s linear;
  -ms-transition: background 0.3s linear, color 0.3s linear;
  -o-transition: background 0.3s linear, color 0.3s linear;
  transition: background 0.3s linear, color 0.3s linear;
}
@media (min-width: 992px) and (max-width: 1199px) {
  .slick-slider .slick-arrow {
    width: 30px;
  }
}
@media (max-width: 767px) {
  .slick-slider .slick-arrow {
    width: 30px;
  }
}
.slick-slider .slick-arrow::before {
  font-family: "icomoon" !important;
  color: #165D8B;
  font-size: 32px;
  opacity: 1;
}
@media (min-width: 1200px) {
  .slick-slider .slick-arrow::before {
    font-size: 40px;
  }
}
.slick-slider .slick-arrow.slick-prev {
  left: 0;
}
.slick-slider .slick-arrow.slick-prev::before {
  content: "\e90f";
}
.slick-slider .slick-arrow.slick-next {
  right: 0;
}
.slick-slider .slick-arrow.slick-next::before {
  content: "\e90e";
}
.slick-slider .slick-arrow:hover {
  color: #9ac53c;
}
.slick-slider .slick-arrow:hover::before {
  opacity: 1;
}
.slick-slider .slick-slide {
  position: relative;
}
.slick-slider .slick-slide img {
  max-width: 100%;
  height: auto;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .slick-slider .slick-slide img {
    width: 100%;
  }
}

.section.primary .slick-slider .slick-arrow::before {
  color: #165D8B;
}
.section.primary .slick-slider .slick-arrow:hover::before {
  color: #fff;
}

.site-wrap .social-share {
  align-items: center;
  display: flex;
  gap: 10px;
  position: relative;
  z-index: 1;
}
.site-wrap .social-share::before, .site-wrap .social-share::after {
  display: none;
}
.site-wrap .social-share span {
  color: #165D8B;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 21px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.site-wrap .social-share a {
  background: #6aab43;
  color: #fff;
  display: inline-block;
  line-height: 43px;
  font-size: 25px;
  text-align: center;
  text-decoration: none !important;
  width: 40px;
  height: 40px;
  -webkit-transition: background 0.3s linear;
  -moz-transition: background 0.3s linear;
  -ms-transition: background 0.3s linear;
  -o-transition: background 0.3s linear;
  transition: background 0.3s linear;
}
@media (max-width: 767px) {
  .site-wrap .social-share a {
    display: inline-block;
  }
}
.site-wrap .social-share a:hover, .site-wrap .social-share a:focus {
  background: #9ac53c;
}

