.base__hero {
  background: #1F1F1F;
  position: relative;
}
.base__hero.has-img {
  border-bottom: none;
  margin-bottom: 0;
}
.base__hero.has-img .img {
  position: relative;
}
.base__hero.has-img .img::before {
  background: rgba(0, 0, 0, 0.5);
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}
.base__hero.has-img .img img {
  aspect-ratio: 2.5/1;
  display: block;
  object-fit: cover;
  width: 100%;
}
@media (max-width: 767px) {
  .base__hero.has-img .img img {
    aspect-ratio: 2.5/3;
  }
}
@media (max-width: 480px) {
  .base__hero.has-img .img img {
    aspect-ratio: 2.5/4;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .base__hero.has-img .img img {
    aspect-ratio: 3/2;
  }
}
@media (min-width: 1400px) {
  .base__hero.has-img .img img {
    aspect-ratio: 3/1;
    object-position: 0% 20%;
  }
}
.base__hero.has-img .img + .blurb {
  bottom: 60px;
  display: inline;
  position: absolute;
  width: 100%;
  z-index: 2;
}
@media (max-width: 991px) {
  .base__hero.has-img .img + .blurb {
    bottom: 0;
    display: block;
    padding: 30px 20px;
  }
}
.base__hero.has-video .img::before {
  background: rgba(0, 0, 0, 0.7) !important;
}
.base__hero.has-video .img img {
  aspect-ratio: 2/1;
  opacity: 0;
}
@media (max-width: 767px) {
  .base__hero.has-video .img img {
    aspect-ratio: 2/3;
  }
}
@media (min-width: 1200px) {
  .base__hero.has-video .img img {
    aspect-ratio: 5/2;
  }
}
@media (max-width: 991px) {
  .base__hero.has-video .img img {
    opacity: 1;
  }
}
.base__hero.has-video .video {
  height: 100%;
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 0;
  width: 100%;
}
@media (max-width: 991px) {
  .base__hero.has-video .video {
    display: none;
  }
}
.base__hero:not(.has-img) {
  background: #1F1F1F;
