.social-wall {
  padding: 50px 0;
  background-color: #1F1F1F;
  color: #fff;
  text-align: center;
}
.social-wall .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
.social-wall .masonry-grid {
  margin: 0 -20px;
  position: relative;
}
.social-wall .post {
  width: 100%;
  padding: 0 20px;
  margin-bottom: 30px;
}
@media (min-width: 1200px) {
  .social-wall .post {
    width: 33.333%;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .social-wall .post {
    width: 50%;
  }
